import { IActionOrTriggerListPageParams } from "@app/static"

import { ParsedUrlQueryInput } from "querystring"
import { IActionGroup, IActionData } from "../action"
import { IProjectData } from "../project"
import { projectStore } from "../storage"
import { ITriggerGroup, ITriggerData } from "../trigger"
import { ITriggerActionGroup, ITriggerAction } from "../trigger-action"
export enum ActionListFromEnum {
    project = 'project',
}
export enum TriggerOrActionListTypeEnum {
    addAction = 'add',
    updateAction = 'update',
}
export interface ITriggerOrActionListPageParams extends ParsedUrlQueryInput{
    // 如果actionType是addAction，则target是taskData的id
    // 如果actionType是updateAction，则target是actionData的id
    // target: string
    // 动作列表里选择动作后要返回的路径
    backPath?: string
    // 本次操作的类型
    type: TriggerOrActionListTypeEnum
    projectId:string
    triggerActionGroupId:string
    triggerActionId:string
    actionGroupId:string
    // 如果是更新，则必须有actionId
    actionId?:string
    // 如果是添加，则必须有actionType
    // actionType为namespace/openApp这种样子的，这样能够从actionMap里找到这个action来进行添加
    // 这个需要在动作列表里选择了具体的action之后才能知道，所以在传递过程中这个可以为空，在pages/action-list/index.tsx里会根据actionId来找到具体的actionType
    actionType:string
    // 处理函数名，在map里
    // handlerFnName:string
}
export interface ITriggerOrActionListParamsData{
    project?:IProjectData
    triggerActionGroup?:ITriggerActionGroup
    triggerAction?:ITriggerAction
    actionGroup?:IActionGroup
    triggerGroup?:ITriggerGroup
    action?:IActionData<any,any>
    trigger?:ITriggerData<any,any>
}
export interface ITriggerOrActionListPageParsedResult{
    params:IActionOrTriggerListPageParams
    data:ITriggerOrActionListParamsData
}
export function getTriggerOrActionListPageParams(params:IActionOrTriggerListPageParams):ITriggerOrActionListPageParsedResult{
    const project = projectStore.findDraftOrSavedProject(params.projectId)
    const data:ITriggerOrActionListParamsData = {}
    const res = {data, params}
    if(!project)return res
    data.project = project
    if(project.triggerActionGroup.id !== params.triggerActionGroupId)return res
    data.triggerActionGroup = project.triggerActionGroup
    if(!params.triggerActionId)return res
    const triggerAction = data.triggerActionGroup.list.find(item=>item.id === params.triggerActionId)
    if(!triggerAction)return res
    data.triggerAction = triggerAction
    parseAction(data, params, triggerAction)
    parseTrigger(data, params, triggerAction)
    return res
    
}

function parseAction(data:ITriggerOrActionListParamsData, params:IActionOrTriggerListPageParams, triggerAction:ITriggerAction){
    if(!params.actionGroupId || !triggerAction.actionGroup || params.actionGroupId != triggerAction.actionGroup.id)return
    data.actionGroup = triggerAction.actionGroup
    const action = triggerAction.actionGroup.list.find(item=>item.id === params.actionId)
    if(!action)return
    data.action = action
}

function parseTrigger(data:ITriggerOrActionListParamsData, params:IActionOrTriggerListPageParams, triggerAction:ITriggerAction){
    if(!params.triggerGroupId || !triggerAction.triggerGroup || params.triggerGroupId != triggerAction.triggerGroup.id)return
    data.triggerGroup = triggerAction.triggerGroup
    const trigger = triggerAction.triggerGroup.list.find(item=>item.id === params.triggerId)
    if(!trigger)return
    data.trigger = trigger
}

